import React, { useEffect, useState } from 'react';
import { useHistory, Route, Redirect  } from 'react-router-dom';
import auth, { checkAuth } from '../../helpers/auth';
import { getCookie } from '../../helpers/cookies';
import ClipLoader from 'react-spinners/ClipLoader';
import classes from './PrivateRoute.module.css';

const PrivateRoute = ({ component: Component, ...rest }) => {

  const history = useHistory();

  const [loggedUser, setLoggedUser] = useState(false);
    
  useEffect(() => {
      const fetchAuth = async() => {
          if(getCookie('ulog') === null) return history.push('/login', { from: { pathname: history.location.pathname } });
              else {
                  const res = await checkAuth();
                  if(res.status === 1) {
                    auth.authenticate(res.username, res.userID);
                    setLoggedUser(true); 
                  } else return history.push('/login', { from: { pathname: history.location.pathname } });
              }
      }

      fetchAuth();
  }, [history]);
    
  if(loggedUser) return <div className={classes.Loading}><ClipLoader size={50} color={'#485877'} /></div>;

  return (
    <Route {...rest} render={props => (
      auth.isAuthenticated === true
        ? <Component {...props} />
        : <Redirect to={{
            pathname: '/login',
            state: { from: props.location }
          }} />
    )} />
)}

export default PrivateRoute;